<template>
  <div class="history">
    <div class="main-820">
      <div class="article-title">
        <h3>风能专业委员会成立<span class="green">38</span>周年大事记回顾</h3>
      </div>
    </div>
    <div class="union-histoty">
      <!-- views -->
      <div class="union-histoty-view">
        <div ref="historyContent" class="union-histoty-view-content" :style="{width: boxLength + 'px', transform: `translate(${leftEnd}px)`}">
          <div v-for="item in list" :key="item.year" class="union-histoty-view-item">
            <div class="left">
              <div class="img"></div>
            </div>
            <div class="right">
              <h5 class="title">{{item.year}}</h5>
              <p class="txt" v-for="p in item.text" :key="p.index">{{p}}</p>
            </div>
          </div>
        </div>
      </div>
      <!-- btn -->
      <div @click="move(1)" class="union-histoty-btn prev">{{listTwoEnd[0].year}}</div>
      <div @click="move(-1)" class="union-histoty-btn next">{{listTwoEnd[1].year}}</div>
    </div>
  </div>
</template>

<script>
import { dataH } from './historyData.js'

export default {
  name: 'History',
  data () {
    return {
      begin: 1000 - dataH.length * 250,
      step: 250,
      list: dataH
    }
  },
  computed: {
    boxLength () {
      return dataH.length * 250
    },
    leftEnd () {
      return 1000 - dataH.length * 250
    },
    listTwoEnd () {
      const lastIndex = dataH.length - 1
      var arr = []
      arr.push(dataH[0])
      arr.push(dataH[lastIndex])
      return arr
    }
  },
  methods: {
    move (t) {
      const content = this.$refs.historyContent
      if (t > 0 && this.begin < 0) {
        this.begin = this.begin + t * 250
      } else if (t < 0 && this.begin > this.leftEnd) {
        this.begin = this.begin + t * 250
      } else {
        return
      }
      content.style.transform = `translate(${this.begin}px)`
    }
  }
}
</script>

